@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono|IBM+Plex+Sans+Condensed:300i,400|Monoton);
body{
    margin:0;
    font-family: 'IBM Plex Mono', monospace;
    cursor: none;
}
.about-section{
    /* To app.css level to share gradient settings */
    min-height: 275px;
    height: 30%;
    background: -webkit-linear-gradient(242deg, #2df1be, #828282, #08958c);
    background: linear-gradient(208deg, #2df1be, #828282, #08958c);
	background-size: 600% 600%;

	-webkit-animation: dynamic-gradient 28s ease infinite;
	animation: dynamic-gradient 28s ease infinite;
}
body a{
    cursor:none!important;
}
.DraftEditor-editorContainer {
    border: 2px solid #bcb4b4;
    margin: 10px 0;
}
.admin{cursor:pointer;}
.container.fluid {
    width: 90%;
    max-width:none;
    max-width:initial;
}
.container.fluid > .row {
margin: 0;
}
.container.fluid > .row > .col {
padding: 0;
}
.project-delete-button{
    position: absolute;
    right: 5px;
    top: 5px;
    padding-left: 4.5px;
    color: #fff;
    background-color: red;
    font-size: 10px;
    border: 1px solid red;
    border-radius: 50%;
    width: 17px;
    height: 17px;
}
.project-content {
    padding: 20px;
    text-align:justify;
}
.project-tags {
    margin: 15px 0;
    text-align:right;
    background: -webkit-linear-gradient(342deg, #2df1be, #828282, #08958c);
    background: linear-gradient(108deg, #2df1be, #828282, #08958c);
}

.project-tags span {
    margin: 0 10px;
    border-left: 2px #fff solid;
    color: #fff;
    padding: 2px 0 0 15px;
}
.flex {
    display: flex;
    flex-wrap: wrap;
  }
.card.selected {
    background-color: #00808029;
}
.animated-loader{
    width: 80%;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: changeBgColorLoader;
            animation-name: changeBgColorLoader;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
.card-panel:nth-child(1) .animated-loader{
    width: 50%;
}
.card-panel:nth-child(2) .animated-loader{
    width: 90%;
}

@-webkit-keyframes changeBgColorLoader {
    from {
        background-color: #ecebeb;
    }
  
    to {
        background-color: #bdbcbc;
    }
  }

@keyframes changeBgColorLoader {
    from {
        background-color: #ecebeb;
    }
  
    to {
        background-color: #bdbcbc;
    }
  }
  
  ::-webkit-scrollbar {
    width: 5px;
}
 

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(209, 209, 209, 0.8); 
    box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

@-webkit-keyframes dynamic-gradient {
    0%{background-position:0% 91%}
    50%{background-position:100% 10%}
    100%{background-position:0% 91%}
}
@keyframes dynamic-gradient { 
    0%{background-position:0% 91%}
    50%{background-position:100% 10%}
    100%{background-position:0% 91%}
}
.about-section .container, .about-section .container .row.flex{
    height:100%;
}

.social  img {
    margin-right: 15px;
    width: 30px;
}
.social img path {fill: #fff;}
.social {
    display: flex;
    padding-top: 18px;
}
.profile-picture img {
    width: 100%;
    padding: 15px;
    height: 100%;
}
.profile-picture{
	width: 125px;
	height: 125px;
	border-radius: 50%;
    border: 2px solid white;
}

.about-section .summary{
    font-size: 16px;
    text-transform: lowercase;
	color: #FFFFFF;
}

.work-section .content {
    margin-top: 55px;
}
.work-section{
    background-color: #fff;
    height: 70%;
    position: relative;
}
.work-section h2{
    color: #000;
    font-family: monoton;
}
.work-section .title{
    margin-top: 25px;
}
.landing{
    width: 100%;
    height: 100vh;
    cursor: pointer;
}
.landing h1{
    font-family: monoton;
    font-size: 45px;
    color: #fff;
    margin: 15px 0;
}
.landing > .row{
    margin-bottom: 0;
}
.summary-text {
    border: 2px solid white;
    padding: 5px;
    font-size: 50px
}
.white-ball{
	width: 25px;
    height: 25px;
    background: #fff;
    border-radius: 50%;
    position: absolute;
    bottom: 8px;
    left: 50%;
}

#cursor {
    position: absolute;
    -webkit-animation: pulse 1s infinite alternate; 
    animation: pulse 1s infinite alternate;
    border: 1px solid #000;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    margin-top: 0em;
    margin-left: 0em;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}
#cursor.expend{
    border-radius: 0%;
    border-width: 3px;
}
#cursor.cursor-enabled {
    border:none;
}
#cursor.cursor-enabled::after {
    content: "click";
    font-weight: bold;
    opacity: .8;
    left: -15px;
    position: absolute;
    -webkit-animation: pulse .8s infinite alternate;
            animation: pulse .8s infinite alternate;
    border:none;
}

@-webkit-keyframes pulse{
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to{
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
}

@keyframes pulse{
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to{
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }
}
@-webkit-keyframes pulseLittle{
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to{
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
}
@keyframes pulseLittle{
    from{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to{
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
}

